import React from 'react';

export default class extends React.Component {

  render() {
    return <div className="body text">
      <p>Спасибо за отзыв.</p>
      <p>Мы обязательно разберемся с проблемой и свяжемся с вами в течении 24 часов.</p>
    </div>
  }

}



